.container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-left: 300px;
  height: 85vh;
  padding-top: 6vh;
  text-align: left;

  .hellopage {
    font-weight: 800;
    font-size: 5rem;
    color: #0d0d0d;
    line-height: 1.3;
    display: flex;
    flex-direction: column;
    letter-spacing: 2px;
    .fullstack {
      font-size: 11vw;
      align-self: flex-start;
    }
    .devloper {
      font-size: 11vw;
      align-self: flex-start;
      margin-left: 300px;
    }
  }
  .intro {
    font-size: 1.2rem;
    line-height: 1.6;
    color: #73706c;
    font-weight: 400;
    margin-top: 40px;
    b {
      text-transform: uppercase;

      font-weight: 400;
    }
  }
}
